.m-link-group {
  display: inline-block;
  vertical-align: top; }
  .m-link-group .u-title {
    color: white;
    margin: 0;
    padding: 5px 0; }
  .m-link-group ul {
    padding: 0;
    margin: 0; }
  .m-link-group .u-item a {
    color: #7a7a7a;
    text-decoration: none;
    line-height: 30px;
    font-size: 14px; }
    .m-link-group .u-item a:hover {
      color: white; }
  .m-link-group .u-item span {
    color: #7a7a7a;
    text-decoration: none;
    line-height: 30px;
    font-size: 14px; }



