.m-result-item {
  min-width: 560px;
  max-width: 960px;
  margin-bottom: 16px; }
  .m-result-item .u-content {
    margin-bottom: 20px; }
    .m-result-item .u-content:after {
      clear: both;
      display: table;
      content: ""; }
  .m-result-item p {
    margin: 0; }
  .m-result-item .u-ip {
    font-size: 25px;
    font-weight: bold;
    color: white;
    display: block;
    margin-bottom: 5px;
    text-decoration: none; }
  .m-result-item .m-left {
    float: left;
    width: 250px;
    overflow: hidden; }
    .m-result-item .m-left .u-tag {
      margin: 5px 0; }
      .m-result-item .m-left .u-tag span + span {
        margin-left: 5px; }
    .m-result-item .m-left .u-country, .m-result-item .m-left .u-org {
      color: #c1bdbf;
      font-size: 13px;
      font-family: sans; }
  .m-result-item .m-right {
    float: left;
    margin-left: 150px;
    max-width: 450px;
    max-height: 500px;
    overflow: hidden; }
    .m-result-item .m-right .u-detail {
      overflow: hidden;
      color: #f7b224;
      font-family: Inconsolata;
      line-height: 25px;
      word-wrap: break-word;
      font-size: 13px; }

:global(.hlt) {
  color: red;
  font-style: normal; }



