.header {
  height: 48px;
  background-color: black;
  padding: 0 40px;
  overflow: hidden; }
  .header .g-left {
    float: left; }
  .header .g-right {
    float: right;
    font-size: 0; }
  .header .link-item {
    line-height: 48px;
    height: 48px;
    color: white;
    font-size: 14px;
    text-decoration: none;
    display: inline-block;
    padding: 0 18px; }
  .header .link-item:hover {
    background-color: #3a3a3a; }
  .header:after {
    content: "";
    clear: both;
    display: table; }

.active {
  background-color: #2a2a2a; }



